.description {
  display: flex;
  align-items: center;
  flex-direction: column;
  padding: 20px;
}

.name,
.tag {
  margin-bottom: 10px;
  font-weight: 700;
  font-size: 30px;
}

.avatar {
  max-width: 200px;
  height: auto;
  margin-bottom: 20px;
  border-radius: 50%;
  box-shadow: rgba(50, 50, 93, 0.25) 0px 50px 100px -20px,
    rgba(0, 0, 0, 0.3) 0px 30px 60px -30px,
    rgba(10, 37, 64, 0.35) 0px -2px 6px 0px inset;
}

.stats {
  display: flex;
  margin: 0 auto;
  padding: 0;
}

.stats > li {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 10px;
  border: 1px solid rgba(33, 33, 33, 0.2);
  border-radius: 6px;
  background-color: #bbc2c9;
  min-width: 100px;
}

.label {
  margin-bottom: 3px;
  font-weight: 600;
}
